import axios from 'axios';
import { toast } from 'react-toastify';
// import authService from './authService';

axios.defaults.baseURL = process.env.REACT_APP_API_URL;
// axios.defaults.headers.common['x-auth-token'] = authService.getJwt();

axios.interceptors.response.use(null, error => {
    const expectedError = error.response && error.response.status >= 400 && error.request.status < 500;
    if(!expectedError){
      toast.error("Brak poczenia z serwerem.")
      setTimeout( () => toast.error("Sprawd czy masz dostp do internetu."), 2500);
      setTimeout( () => toast.error("Jeeli internet dziaa, powiadom administratora programu o problemie."), 5000);
  }
  return Promise.reject(error)
})

function setJwt(jwt){
  console.log(jwt, 'jwt')
  axios.defaults.headers.common['x-auth-token'] = jwt;
}
export default {
  get: axios.get,
  post: axios.post,
  put: axios.put,
  patch: axios.patch,
  delete: axios.delete,
  setJwt
}