import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import ConfirmAlert from './common/confirmAlert.js';
import userService from '../services/userService';
import { toast } from 'react-toastify';
class LogoutButtonBox extends Component {
    state = { 
        user: {
            name: "",
            password: ""
        },
    }
    form_confirmAlert = {
        title: "Wylogowanie",
        btn_label: <i className="fa fas fa-sign-out"></i>,
        btn_css: "btn btn-outline-secondary btn-sm",
        action: () =>this.logout()
    };

    componentDidMount(){
        const user = userService.getUserFromJWT();
        this.setState({ user });
    }
    
    logout =  async () => {
        try{
            await userService.logoutUser();
            window.location = "/";
        }catch (ex){
            toast.error("Nie udao si poprawnie wylogowa");
        }
        
    };

    handleInputOnChange = ({ currentTarget: input }) => {
        const user = {...this.state.user};
        user[input.name] = input.value;
        this.setState({ user })
    };

    render_pLabel(){
        const { name } = this.state.user;
        const index = name.indexOf(" ") - 1;
        if(name[index] === "a") return "Jeste zalogowana jako:";
        return "Jeste zalogowany jako:"
    };
    
    render() { 
        const { user } = this.state;
        return (
            <div className="logout-button-box">
                <div className="logout-button-box__info">
                    <p>{this.render_pLabel()}</p>
                    <Link to="/settings/user">
                        <p>{user.name}</p>
                    </Link>
                </div>
                <ConfirmAlert 
                        item={user}
                        itemRepresentation="Czy chcesz si wylogowa?"
                        form = {this.form_confirmAlert}
                        />
            </div>
         );
    }
}
 
export default LogoutButtonBox;