import httpService from "./httpService";
import { toast } from "react-toastify";

const apiEndPoint = "/tasks";

export async function getTasks(){
    try{
        const { data } = await httpService.get(apiEndPoint);
        return data;
    }catch(ex){
        if(ex && ex.response) toast.error(ex.response.data)
        else toast.error("Nie mona uzyska danych o zdarzeniach.")
    }
}

export async function addTask(task){
    const { data } = await httpService.post(apiEndPoint, task);
    return data;
}
export async function updateTask(taskToUpdate){
    const { data } = await httpService.put(apiEndPoint + "/" + taskToUpdate._id, taskToUpdate );
    return data;
}
export async function deleteteTask(taskToUpdate){
    const { data } = await httpService.delete(apiEndPoint + "/" + taskToUpdate._id );
    return data;
}

export default {
    getTasks,
    addTask,
    updateTask,
    deleteteTask
}