import React from 'react';
import UserInfoLogin from './logoutButtonBox';
import LinkWithButton from './common/linkWithButton.js';

const NoPermissions = () => {
    return ( 
        <React.Fragment>
            <header className="app__header"></header>
            <div className="app__content">
                <div className="app__container-buttons">
                    <div></div>
                    <UserInfoLogin />
                </div>
                <div className="no-permissions-box">
                    <h2>ODMOWA DOSTPU</h2>
                    <p>Nie posiadasz uprawnie do przegldania zawartoci tej strony.</p>
                    <LinkWithButton label="Przejd do wyboru lokalizacji" path="/select-localisation" />
                </div>
            </div>
        </React.Fragment>
     );
}
 
export default NoPermissions;