import React, { Component } from 'react';
import ShowDataHeader from './showData_Header';
import localisationService from '../services/localisationService';
import SortingButtons from './showData_SortingButtons';
import LogoutButtonBox from './logoutButtonBox';
import TaskBox from './showData_TaskBox';
import Loader from './common/loader';
import taskService from '../services/taskService';
import userService from '../services/userService';
import { toast } from 'react-toastify';

class ShowData extends Component {
      state = {
            data: [], /// musi tu by a nie w TaskBox, bo funkcja addTask jest w showdataHeader
            currentSortingStatus: false, // oznacza, ktry przycisk ma si wywietli jako domylny / biecy
            currentLocalisation: { name: "" },
            validLocalisations: [],
            deleteTaskMode: false,
            serviceMode: false,
            serviceMode_jobName: "Admin"
      };
      isSupervisor = userService.isCurrentUserGreaterThanORequalTo("Koordynator");

      async componentDidMount(){
            this.redirectIfNeeded();
            try{
                  const data = await taskService.getTasks();
                  const validLocalisations = await localisationService.getVerifiedUserLocalisations();
                  const currentLocalisation = this.getCurrentLocalisation(validLocalisations);
                  this.setState({ data, validLocalisations, currentLocalisation });
            }catch(ex){
                  if(ex && ex.response) toast.error("Problem z pobraniem danych z serwera.")
            }
            this.set_Interval_ToRefreshData()// uruchomienie odwieania danych
      };
      componentWillUnmount(){
            this.mounted = false;
            clearInterval( this.run_interval_refreshData );
      };

      // handlers
      handleChangeDeleteTaskMode = () => {
            let { deleteTaskMode } = this.state;
            deleteTaskMode = !deleteTaskMode;
            this.setState({ deleteTaskMode });
      }
      handleChangeSortingStatus = currentSortingStatus => {
            this.setState({ currentSortingStatus });
      };
      handleChangeServiceMode = () => {
            this.setState({ serviceMode: !this.state.serviceMode, serviceMode_jobName: userService.getUserFromJWT().job })
      };
      handleChangeServiceModeJob = serviceMode_jobName => {
            this.setState({ serviceMode_jobName })
      };
      handleChangeCurrentLocation = currentLocalisation => {
            this.setState({ currentLocalisation });
      }
      handleAddTask = async (task) => {
            task.location = this.state.currentLocalisation.name;
            task.createdBy = userService.getUserFromJWT().name;
            const newTask = await taskService.addTask(task);
            this.updateTaskList(newTask);
      }
      handleDeleteTaskFromDataBase = async (taskToDelete) => {
            const task = await taskService.deleteteTask(taskToDelete);
            this.updateTaskList(task);
      }
      handleCloseTask = async (task) => {
            const arrayOfUpdateDetails = [
                  {property: "isDone", value: true },
                  {property: "closedBy", value: userService.getUserFromJWT().name}
            ]
            this.handleUpdateTask(task, arrayOfUpdateDetails);
      };
      handleTakeTask = async (task) => {
            const arrayOfUpdateDetails = [ {property: "takedBy", value: userService.getUserFromJWT().name} ]
            this.handleUpdateTask(task, arrayOfUpdateDetails);
      };
      handlePartialDelivery = async (task) => { // usun async
            const arrayOfUpdateDetails = [
                  {property: "partialQty", value: task.partialQty },
                  {property: "partialTime", value: 0 }
            ]
            this.handleUpdateTask(task, arrayOfUpdateDetails);
      }
      handleUpdateTask = async (task, arrayOfUpdateDetails ) => {
            arrayOfUpdateDetails.forEach( t => task[t.property] = t.value );
            task = await taskService.updateTask(task);
            const data = [ ...this.state.data ];
            let index = data.findIndex( t => t._id === task._id);
            data[index] = task;
            this.setState( { data })
      }
      // services
      set_Interval_ToRefreshData(){
            this.mounted = true;
            this.run_interval_refreshData();
      };
      run_interval_refreshData(){
            setInterval( async () => {
            if(this.mounted) this.setState({ data: await taskService.getTasks() }); }, 10000)
      };
      async redirectIfNeeded(){
            const seletedLocalisation = await localisationService.getSelectedLocalisation(this.props.match.params.id);
            if( !seletedLocalisation && !this.isSupervisor ) window.location = "/select-localisation";
      };
      getCurrentLocalisation(validLocalisations){
            const localisationFromPathname = validLocalisations.filter( vl => vl.path === this.props.location.pathname );
            return localisationFromPathname[0] ? localisationFromPathname[0] : validLocalisations[0];
      }
      updateTaskList(task){
            let { data } = { ...this.state };
            const index = data.findIndex( t => t._id === task._id );
            if(index === -1) data.unshift(task);
            else data.splice(index, 1);
            this.setState({ data })
      }
      
      // render
      render() { 
            const { currentLocalisation, currentSortingStatus, validLocalisations, data,
                  deleteTaskMode, serviceMode, serviceMode_jobName } = this.state;
            return ( 
                  <React.Fragment>
                        <ShowDataHeader
                              currentLocation={currentLocalisation}
                              validLocations={validLocalisations}
                              serviceMode={serviceMode}
                              serviceMode_jobName={serviceMode_jobName}
                              onChangeCurrentLocation={this.handleChangeCurrentLocation}
                              onhandleAddTask={this.handleAddTask}
                              onhandleChangeServiceMode = {this.handleChangeServiceMode}
                              onhandleChangeServiceModeJob={this.handleChangeServiceModeJob}
                              />
                        {data && data.length === 0 && <Loader /> }
                        {data && data &&
                        <div className="app__content">
                              <div className="app__container-buttons">
                                    <SortingButtons 
                                          currentSortingStatus={ currentSortingStatus }
                                          deleteTaskMode={ deleteTaskMode }
                                          onChangeSortingStatus={ this.handleChangeSortingStatus }
                                          onhandleChangeDeleteTaskMode={ this.handleChangeDeleteTaskMode }
                                          />
                                    <LogoutButtonBox />
                              </div>
                                    <TaskBox
                                          data={data}
                                          currentLocation={ currentLocalisation }
                                          currentSortingStatus={ currentSortingStatus }
                                          deleteTaskMode={ deleteTaskMode }
                                          serviceMode={serviceMode}
                                          serviceMode_jobName={serviceMode_jobName}
                                          onhandleCloseTask={this.handleCloseTask}
                                          onhandleTakeTask={this.handleTakeTask}
                                          onhandlePartialDelivery={this.handlePartialDelivery}
                                          onhandleDeleteTaskFromDataBase={this.handleDeleteTaskFromDataBase}
                                          />
                        </div>
                        }
                  </React.Fragment>
            );
      }
}
 
export default ShowData;