import React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import Joi from 'joi-browser';
import ConfirmAlertInput from './common/confirmAlertInput.js';
import { toast } from 'react-toastify';

class ConfirmAlertSettings extends ConfirmAlertInput {
    state = { item: { name: "" } };

    schema = {
      name: Joi.string().required().error( () => {return { message: this.errorMessages["name"]};}),
    };

    errorMessages = {
      name: 'Nazwa nie moe by pusta.'
    }
    doSubmit(onClose){
      const {item} = this.state;
      const error = this.validate(item);
      if(error) { toast.error(error); return null; }
      this.props.form.action(item);
      onClose();
  }
    submit = () => {
        confirmAlert({
            customUI: ({ onClose }) => {
              const { title, p_label } = this.props.form;
              return (
                <div className="confirmAlertInput">
                  <h1>{title}</h1>
                  <div className="confirmAlertInput__input-box">
                      {this.renderInputBoxGroup(p_label, "name", "text", "Podaj dane", onClose, "autoFocus")}
                  </div>
                  <div className="confirmAlertInput__button-group">
                      {this.renderButton("Dodaj", () => this.doSubmit(onClose) ) }
                      {this.renderButton("Anuluj", onClose ) }
                  </div>
                </div>
              )
            } })
      };
    
    render() { 
        const { btn_css, btn_label } = this.props.form;
        return this.renderButton(btn_label, this.submit, btn_css ) ;
    }
}

export default ConfirmAlertSettings;