import React from 'react';
import { Redirect } from 'react-router-dom';
import LinkWithButton from './common/linkWithButton.js';
import LogoutButtonBox from './logoutButtonBox';
import Advertising from './advertising';
import userService from '../services/userService';

const Home = () => {
    const user = userService.getUserFromJWT();
    if(!user) return <Redirect to="/login" />
    
    const isSupervisor = userService.isCurrentUserGreaterThanORequalTo("Koordynator");

    return ( 
        <React.Fragment>
            <header className="app__header">
                <div className="app__header-title">FILMAG</div>
            </header>
            <div className="app__content">
                <div className="app__container-buttons">
                    <div className="settings__switch-buttons">
                        <LinkWithButton label="Wywietl zdarzenia" path="/show-data" css={"btn btn-sm btn-primary"} />
                        { isSupervisor && <LinkWithButton label="Przejd do ustawie" path="/settings/users" /> }
                    </div>
                    <LogoutButtonBox />
                </div>
                <Advertising />
            </div>
        </React.Fragment>
     );
}
 
export default Home;