import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import jwtDecode from 'jwt-decode';
import ShowData from './components/showData';
import Settings from './components/settings';
import Home from './components/home';
import SelectLocalisation from './components/selectLocalisation';
import ProtectedRoute from './components/common/ProtectedRoute.js';
import LoginPage from './components/loginPage';
import NoPermissions from './components/noPermssions';
import 'react-toastify/dist/ReactToastify.css';
// import userService from './services/userService';

class App extends Component {
  state = { };

  componentDidMount(){
    try{
      const jwt = localStorage.getItem("token");
      const user = jwtDecode(jwt);
      this.setState({ user });
      // window.addEventListener("onbeforeunload", this.logoutUserWhenLeave);
  
    }
    catch (ex) {} // ignoruj brak tokenu
  }

  // logoutUserWhenLeave = e => {
  //   e.preventDefault();
  //   userService.logoutUser();
  // };
  
  render() {
    document.title = "FILMAG";
    return (
        <div className="App">
            <ToastContainer />
            <Switch>
              <ProtectedRoute path="/show-data/:id" requireUserLevel={"Operator"} redirectPath="/login" component={ShowData}/>
              <ProtectedRoute path="/show-data" requireUserLevel={"Operator"} redirectPath="/login" component={ShowData}/>
              <ProtectedRoute path="/select-localisation" requireUserLevel={"Operator"} redirectPath="/login" component={SelectLocalisation}/>
              <ProtectedRoute path="/settings" requireUserLevel={"Operator"} redirectPath="/no-permissions" component={Settings}/>
              <ProtectedRoute path="/no-permissions" requireUserLevel={"Operator"} redirectPath="/login" component={NoPermissions}/>
              <Route path="/login" component={LoginPage} />
              <Route path="/" component={Home} />
            </Switch>
        </div>
      );
    }
}

export default App;