import React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import Joi from 'joi-browser';
import ConfirmAlertInput from './common/confirmAlertInput.js';
import { toast } from 'react-toastify';

class ConfirmAlertInput_task extends ConfirmAlertInput {
    state = {};
    schema = {
        content: Joi.string().max(40).required().error( () => {return { message: this.errorMessages["content"]};}),
        type: Joi.string().required(),
        location: Joi.string(),
        createdBy: Joi.string()
    };
    errorMessages = {
        content: 'Tre zdarzenia nie moe by pusta i moe mie maks. 40 znakw.',
        qty: 'Pole "Ilo" nie moe by puste a warto by liczb z zakresu 1 - 9999.'
    }
    componentDidMount(){
        const item = {
          content: this.props.form.content,
          type: this.props.button.type,
        }
        if(this.props.button.type === "order"){
          this.addQtyToSchema();
          item.qty = null;
        }
        this.setState({ item })
    }
    addQtyToSchema(){
        return this.schema.qty = Joi.number().min(1).max(9999).required().error( () => {return { message: this.errorMessages["qty"] };});
    }
    resetTaskData(){
      let item = { ...this.state.item };
      item.content = this.props.form.content;
        if(this.props.button.type === "order"){
          item.qty = "";
        }
      this.setState({ item })
    }
    doSubmit(onClose){
        const {item} = this.state;
        const error = this.validate(item);
        if(error) { toast.error(error); return null; }
        this.props.form.action(item);
        onClose();
    }
    submit = () => {
      this.resetTaskData();
      confirmAlert({
        customUI: ({ onClose }) => {
              const { title, content, p_label, p_label_2 } = this.props.form;
              return (
                <div className="confirmAlertInput">
                  <h1>{title}</h1>
                  <div className="confirmAlertInput__input-box">
                    {this.renderInputBoxGroup(p_label, "content", "text", content, onClose, "autoFocus")}
                    { p_label_2 &&  this.renderInputBoxGroup(p_label_2, "qty", "number", "Podaj ilo", onClose) }
                  </div>
                  <div className="confirmAlertInput__button-group">
                    {this.renderButton("Dodaj", () => this.doSubmit(onClose) ) }
                    {this.renderButton("Anuluj", onClose ) }
                  </div>
                </div>
              )
            } })
      };
    
      render() { 
          return ( 
                <div className="app__header_headerButtons-wrap">
                  {this.renderButton(this.props.button.label, this.submit, "btn btn-secondary" ) }
                </div>
              );
    }
}

export default ConfirmAlertInput_task;