import React, { Component } from 'react';
import LinkWithButton from './common/linkWithButton.js';
import userService from '../services/userService';
import ConfirmAlertSettings from './confirmAlertSettings';
import LogoutButtonBox from './logoutButtonBox';
import { toast } from 'react-toastify';
import Joi from 'joi-browser';

class UserPage extends Component {
    state = { 
        user: {}
    }
    
    form_resetPassword = {
        title: "Zmie haso",
        p_label: "Haso",
        btn_label: "Zmie haso",
        btn_css: "btn btn-outline-danger btn-sm",
        action: (pwd) => this.handleResetPassword(pwd)
    };
    schema = {
        password: Joi.string().min(5).required().error( () => {return { message: "Podane haso jest za krtkie. Minimum to 5 znakw."};})
    };

    async componentDidMount() {
        const user = userService.getUserFromJWT();
        this.setState({ user });
    };

    async handleResetPassword({ name: newPassword }){
        let user = { ...this.state.user };
        user.password = newPassword;
        const error = this.validateNewPassword({password: newPassword}, this.schema);
        if(error) { 
            toast.error(error);
            return null;
         }
        user = await userService.resetUserPassword(user);
        toast.success("Zaktualizowano haso");
        this.setState({ user })
    };
    validateNewPassword(user){
        const { error } = Joi.validate(user, this.schema);
        return error ? error.details[0].message : null;
    };


    renderSettingsNavigationButtons(){
        return  <div className="settings__switch-buttons">
                    <LinkWithButton label="Wywietl zdarzenia" path="/show-data" css={"btn btn-sm btn-primary"} />
                    <LinkWithButton label="Uytkownicy" path="/settings/users" />
                    <LinkWithButton label="Lokalizacje" path="/settings/localisations" />
                </div>
    };
renderReturnButton(){
    return <button className="btn btn-primary btn-sm" onClick={ this.props.history.goBack }>Powr do wybranej lokalizacji</button>
}
    render() { 
        const { permissions, name: userName } = userService.getUserFromJWT();
        const isSupervisor = userService.isCurrentUserGreaterThanORequalTo("Koordynator");
        const validLocalisations = permissions.length > 0 ? permissions.join(', ') : "Brak uprawnie.";
        return ( 
        <div className="app__content">

            <div className="app__container-buttons">
                { !isSupervisor && this.renderReturnButton() }
                { isSupervisor && this.renderSettingsNavigationButtons() }
                <div></div>
                <LogoutButtonBox />
            </div>

            <div className="settings__user-page">
                <div className="settings__user-page_user_box">
                    <h2>Profil uytkownika</h2>
                    
                    <div className="settings__user-page_user_box__info-box">
                        <div className="settings__user-page_user_box__info-box_group">
                            <p>Login:</p>
                            <div>{ userName }</div>
                        </div>
                    </div>

                    <div className="settings__user-page_user_box__info-box_group">
                        <p>Haso :</p>
                        <div><ConfirmAlertSettings form={this.form_resetPassword} /></div>
                    </div>

                    <div className="settings__user-page_user_box__info-box">
                        <div className="settings__user-page_user_box__info-box_permissions">
                            <p>Masz dostp do lokalizacji typu:</p>
                            <div>- { validLocalisations }.</div>
                        </div>
                    </div>

                </div>
            </div>
        </div>

         );
    }
}
 
export default UserPage;