import React from 'react';
import ConfirmAlert from './common/confirmAlert.js';
import userService from '../services/userService';
import utils from '../services/utils';
import ConfirmAlertInputPartialDelivery from './confirmAlertInputPartialDelivery';

const Task = (props) => {

    const form_closeTask = {
        title: "Potwierd zakoczenie zdarzenia",
        btn_label: <p><span className="task-box_btn-label_full">Zakocz</span><span className="task-box_btn-label_short">Z</span></p>,
        btn_css: "btn-danger btn-sm",
        action: (task) => props.onhandleCloseTask(task)
    };
    const form_deleteTask = {
        title: "Potwierd usunicie zdarzenia z rejestru",
        btn_label: <p><span className="task-box_btn-label_full">Usu</span><span className="task-box_btn-label_short">X</span></p>,
        btn_css: "btn-outline-danger btn-sm",
        action: (task) => props.onhandleDeleteTaskFromDataBase(task)
    };
    const form_partialDelivery = {
        title: "Zarejestruj bierzc dostaw",
        btn_label: <p><span className="task-box_btn-label_full">Dostawa</span><span className="task-box_btn-label_short">D</span></p>,
        btn_css: "btn-primary btn-sm",
        action: (task) => props.onhandlePartialDelivery(task)
    };
    // render functions
    function renderTaskStatus(task){
        const statusCellStyle = task.isDone ? "status_dot-ok" : "status_dot-ng";
        return <div className={ statusCellStyle }></div>
    }
    function renderButton(task, form){
        return <ConfirmAlert item ={task} itemRepresentation={task.content} form={form} />
    };
    function renderDeliveryButton(task){
        return <ConfirmAlertInputPartialDelivery task={task} form={form_partialDelivery} />
    };
    function renderQty(task){
        const alreadyDelivered = task.partialQty ? task.partialQty : 0;
        return  <React.Fragment>
                    <div className="task-box__task_qty-main">{ task.qty }</div>
                    { !task.isDone && <div className="task-box__task_qty-partialDelivery">({ alreadyDelivered })</div> }
                </React.Fragment>
    };
    function renderTaskContent(task){
        // function getServiceMode(){
        //     if(userService.getUserJobValue(props.serviceMode_jobName) >= 10) return false;
        //     return true;
        // }
        // const isRenderTooltip = props.serviceMode ? getServiceMode() : userService.isCurrentUserLessThan("Koordynator");
        // const content = task.takedBy ? task.takedBy : "Nie przypisano osoby";

        return  <React.Fragment>
                    <div className="task-box__task_content-text">{ task.content }</div>
                    <div className="task-box__task_content-createdInFormatedData">{ task.createdInFormatedData }</div>
                    {/* { (task.type === "order" || task.type === "orderTakeout") && isRenderTooltip && <p className="task-box__tooltip">{content}</p> } */}
                </React.Fragment>
    }
    function renderContent (task, column, index) {
             if(column.path === "#") return index;
        else if(column.path === "content") return renderTaskContent(task);
        else if(column.path === "status") return renderTaskStatus(task);
        else if(column.path === "qty") return task.type === "order" ? renderQty(task) : "";
        else if(column.path === "partialQty") return task.type === "order" ? task.partialQty : "";
        else if(column.path === "location") return task.location;
        else if(column.path === "time") return task.time >= 1400 ? "24h+" : task.time;
        else if(column.path === "takedBy") return renderTakedBy(task);
        else if(column.path === "btnClose" && !task.isDone ) return renderButton(task, form_closeTask);
        else if(column.path === "take" && !task.isDone){
                if(task.type === "order" && task.takedBy === userService.getUserFromJWT().name) return renderDeliveryButton(task);
           else if(task.type === "order" || task.type === "orderTakeout" ) return renderButton(task, getCustomForm(task));
        }
        else if(column.path === "createdInFormatedData") return task.createdInFormatedData ;
        else if(column.path === "btnDelete") return renderButton(task, form_deleteTask) ;
    };
    function renderTakedBy(task){
        return task.type === "order" || task.type === "orderTakeout" ? (task.takedBy ? task.takedBy : "?" ) : task.createdBy;
    }
    // service
    function getCustomForm(item){
        const currentUserTakedThisTask = task.takedBy === userService.getUserFromJWT().name;
        const btnStyle_taskTaked = currentUserTakedThisTask && !task.isDOne ? "btn-primary btn-sm" : "btn-outline-secondary btn-sm";
        const fullText = currentUserTakedThisTask ? "Obsugujesz" : (item.takedBy ? "Przejmij" : "Bierz zlecenie");
        const shortText =  currentUserTakedThisTask ? "T" : (item.takedBy ? "P" : "B");
        const title = !task.takedBy ? "Potwierd przyjcie zlecenia" : (task.takedBy !== userService.getUserFromJWT().name ? "Potwierd przejcie zlecenia" : ""); 
        const btn_label = <div><span className="task-box_btn-label_full">{ fullText }</span><span className="task-box_btn-label_short">{ shortText }</span></div>;
        const btn_css = item.takedBy ? btnStyle_taskTaked : "btn-warning btn-sm";
        return {
                title: title,
                btn_label: btn_label,
                btn_css: btn_css,
                isDisabled: currentUserTakedThisTask,
                action: (task) => props.onhandleTakeTask(task)
               };
    };
    function taskCustomCSS(task){
             if( task.isDone ) return "task-box__task-color_ok";
        else if( task.partialTime >= 10 || task.type === "breakdown" ) return "task-box__task-color_ng";
        else if( task.type === "modelChange" || task.type === "cleaning" ) return "task-box__task-color_warning";
        else return "";
    };
    // return
    const { task, columns, index } = props;
    return ( 
        <div className={"task-box__task " + taskCustomCSS(task) } >
            { columns.map( column => 
                <div key={ task._id + column.path } className={ utils.getCustomTaskCss(column.path, "task") }>
                    { renderContent(task, column, index)}
                </div>
            )}
        </div>
        );
}
 
export default Task;