import React from 'react';

const InputCustomSelect = ({ options, currentValue, item, isDisabled = false, onChange, selectOptions }) => {
    const selectedOptions = selectOptions ? selectOptions(options, currentValue) : options;
    return ( 
        <div className="">
            <select className="custom-select" 
                name={item}
                onChange={ onChange}
                value = {currentValue}
                disabled={ isDisabled }
                >
                {selectedOptions.map(option => <option key={option.name} disabled={option.isDisabled} className="a" value={ option.value }>{option.name}</option>)}
            </select>
        </div> 
     );
}
 
export default InputCustomSelect;