import httpService from "./httpService";
import jwtDecode from 'jwt-decode';
import userService from '../services/userService';
import { toast } from 'react-toastify';

const apiEndPoint = "/localisations";

const localisationsPriorityValues = {
    "Magazyn": 3,
    "Utrzymanie ruchu": 2,
    "Maszyna": 1
};

export async function getLocalisations(){
    try{
        let { data } = await httpService.get(apiEndPoint);
        return data.sort( (a,b) => getLocalisationPriorityValue(b.category) - getLocalisationPriorityValue(a.category) );
    }catch(ex){
        toast.error("Nie mona uzyska danych o lokalizacjach.")
    }
}
export async function addLocalisation(localisation){
    const { data } = await httpService.post(apiEndPoint, localisation);
    return data;
}
export async function updateLocalisation(localisation){
    const { data } = await httpService.put(apiEndPoint + "/" + localisation._id, localisation );
    return data;
}
export async function deleteLocalisation(localisation){
    const { data } = await httpService.delete(apiEndPoint + "/" + localisation._id );
    return data;
}

export function getUserPermisions_fromJWT(){ // wyrzuci ?
        const jwt = localStorage.getItem("token");
        return jwtDecode(jwt).permissions;
}

export async function getVerifiedUserLocalisations(){
    const user = userService.getUserFromJWT();
    const localisations = await getLocalisations();
    return localisations.filter( l => user.permissions.indexOf( l.category ) >= 0 )
}

export async function getSelectedLocalisation(endPoint){
    const validLocalisations = userService.getUserFromJWT().validLocalisations;
        return endPoint ? validLocalisations.filter( vl => vl.name === endPoint )[0] : null;

}
export function getLocalisationPriorityValue(localisationCategory){
    return localisationsPriorityValues[localisationCategory]
}

export default {
    getLocalisations,
    addLocalisation,
    updateLocalisation,
    deleteLocalisation,
    getUserPermisions_fromJWT,
    getSelectedLocalisation,
    getLocalisationPriorityValue,
    getVerifiedUserLocalisations
}