import React from 'react';
import Task from './showData_Task';
import { FixedSizeList } from "react-window";
import AutoSizer from "react-virtualized-auto-sizer";
import userService from '../services/userService.js';
import utils from '../services/utils';

const TaskBox = (props) => {
 
    const columns = [
        {label: "#", path: "#"},
        {label: "Zdarzenie", path: "content"},
        {label: "Status", path: "status"},
        {label: "Ilo", path: "qty"},
        {label: "Dostarczono", path: "partialQty"},
        {label: "Miejsce", path: "location"},
        {label: "Czas", path: "time"},
    ];
    const specialColumns = [
        {label: "", path: "take"},
        {label: "", path: "btnClose"},
        {label: "Data rejestracji", path: "createdInFormatedData"},
        {label: "Obsuguje", path: "takedBy"},
        {label: "", path: "btnDelete"}
    ];

    function isServiceMode_jobName(jobName){
        if(jobName === "Koordynator") return userService.getUserJobValue(props.serviceMode_jobName) >= userService.getUserJobValue(jobName);
        return props.serviceMode_jobName === jobName;
    }

    function getFilterData_byLocation(data){
        const { currentLocation } = props;
        if(currentLocation.category === "Magazyn") return data
        else if(currentLocation.category === "Utrzymanie ruchu") return data.filter( item => item.type === "breakdown")
        else return data.filter( item => item.location === currentLocation.name);
    };

    function getFilteredData_bySortingStatus(data){
        let { currentSortingStatus } = props;
        if(currentSortingStatus === "All") return data;
        return data.filter( item => item.isDone === currentSortingStatus);
    };
    function getFilteredColumns(){
        let filteredColumns = [ ...columns];
        const { currentLocation } = props;
        filteredColumns.push( specialColumns[3] )
        if( !props.serviceMode ? userService.isCurrentUserEqualTo("Magazynier") : isServiceMode_jobName("Magazynier") ) filteredColumns.push( specialColumns[0]);
        if( !props.serviceMode ? userService.isCurrentUserEqualTo("Operator") : isServiceMode_jobName("Operator") ) filteredColumns.push( specialColumns[1]);
        if( currentLocation.category !== "Utrzymanie ruchu")
            if( !props.serviceMode ? userService.isCurrentUserGreaterThanORequalTo("Koordynator") : isServiceMode_jobName("Koordynator") ) filteredColumns.push( specialColumns[2] );
        if( props.deleteTaskMode ){
            filteredColumns.push( specialColumns[1]);
            filteredColumns.push( specialColumns[4])
        }
                
        return filteredColumns;
    };
    function getSortedData(){
        let { data } = props;
        data = getFilterData_byLocation(data);
        data = getFilteredData_bySortingStatus(data);
        return data;
    };

    function renderTaskBoxHeader(columns){
        const invisibleText = props.currentLocation.name === "UTRZYMANIE RUCHU" ? " invisible" : "";
        function isVisible(column){
            return column.path === "qty" ? invisibleText : "";
        }

        return <div className={"task-box__header"}>
                    { columns.map( column =>
                        <div key={column.path} className={ utils.getCustomTaskCss(column.path, "header") + isVisible(column) }
                        >{column.label}</div> ) }
               </div> ;
    };

    function renderTasks(data, columns){
        return <AutoSizer >
                    {({ height, width }) => (
                    <FixedSizeList
                            itemData={data}
                            height={height - 50}
                            width={width}
                            itemCount={data.length}
                            itemSize={43}
                        >
                        {({data, index, style }) => {
                            const task = data[index];
                            return (
                                <div style={{ ...style }}>
                                <Task
                                    key={task._id}
                                    index={index + 1}
                                    task={task}
                                    columns={columns}
                                    serviceMode={props.serviceMode}
                                    serviceMode_jobName={props.serviceMode_jobName}
                                    onhandleCloseTask={props.onhandleCloseTask}
                                    onhandleTakeTask={props.onhandleTakeTask}
                                    onhandlePartialDelivery={props.onhandlePartialDelivery}
                                    onhandleDeleteTaskFromDataBase={props.onhandleDeleteTaskFromDataBase}
                                    >
                                </Task>
                                </div>
                        )}}
                    </FixedSizeList>
                    )}
                </AutoSizer>
    };

    const data = getSortedData();
    const filteredClumns = getFilteredColumns();
    return ( 
        <div className="task-box">
            { renderTaskBoxHeader( filteredClumns ) }
            { renderTasks(data, filteredClumns) }
        </div>
    );
}

export default TaskBox;
