import React from 'react';
import { Switch, Redirect } from 'react-router-dom';
import SettingsHeader from './settings_Header';
import SettingsUsers from './settings_Users';
import SettingsLocalisations from './settings_Localisations';
import UserPage from './settings_User';
import ProtectedRoute from './common/ProtectedRoute.js';

const Settings = () => {
    const submenuOptions = [
        { label: "Wykaz uytkownikw", path: "/settings/users" },
        { label: "Wykaz lokalizacji", path: "/settings/localisations" },
        { label: "Uprawnienia uytkownikw", path: "/settings/permissions" },
        { label: "Mj profil", path: "/settings/user" }
    ]
    
    return ( 
        <React.Fragment>
            <SettingsHeader validLocations={ submenuOptions } />
            <Switch>
                <ProtectedRoute path="/settings/localisations" requireUserLevel={"Koordynator"} component={SettingsLocalisations} redirectPath="/no-permissions"/>
                <ProtectedRoute path="/settings/users" requireUserLevel={"Koordynator"} component={SettingsUsers} redirectPath="/no-permissions"/>
                <ProtectedRoute path="/settings/user" requireUserLevel={"Operator"} component={UserPage} redirectPath="/login"/>
                <Redirect from="/settings" to="/settings/users" />
            </Switch>
        </React.Fragment>
        );
    }
 
export default Settings;