import React from 'react';
import ConfirmAlertInputTask from './confirmAlertInputTask';
import userService from '../services/userService';
import MenuMainItem from './common/menuMainItem.js';
import MenuItems from './common/menuItems.js';

const ShowDataHeader = (props) => {

    const headerButtons = [
        {label: "Zamw formatk", type: "order"},
        {label: "Odbir palet", type: "orderTakeout"},
        {label: "Awaria", type: "breakdown"},
        {label: "Przezbrojenie", type: "modelChange"},
        {label: "Czyszczenie", type: "cleaning"}
    ];
    const serviceModeButtons = [
       "Operator",
       "Magazynier",
       "Prac. utrz. ruchu",
       "Koordynator",
       "Mistrz",
       "Kierownik",
       "Dyrektor",
       "Prezes",
       "Admin",
       "SuperAdmin"
    ]
    const form_confirmAlertInput_addTask = {
        title: "Dodaj zdarzenie",
        p_label: "Tre",
        btn_css: "btn btn-success btn-sm",
        action: (item) => props.onhandleAddTask(item),
     };

    function renderCustomForm(button){
        let form = { ...form_confirmAlertInput_addTask };
        form.content = button.label === "Zamw formatk" ? "" : button.label;
        form.p_label_2 = button.type === "order" ? "Ilo" : "";
        return  form;
    };

    function renderShowDataMenu(){
        const { validLocations, onChangeCurrentLocation } = props;
        const isAdmin = userService.isCurrentUserGreaterThanORequalTo("Admin");
        return <div className="app__header-barIcon" >
                    <i className="fa far fa-bars fa-3x"></i>
                    <ul className="app__header-menu">
                        {isAdmin && renderServiceModeMainButton() }
                        <MenuMainItem label="Ustawienia" path="/settings/" icon="fa-cog" />
                        <MenuItems items={validLocations} action={ onChangeCurrentLocation }/>
                    </ul>
                </div>
    };
    function renderServiceModeMainButton(){
        const toggle = props.serviceMode ? "fa fa-toggle-on" : "fa fa-toggle-off";
        return <li className="app__header-menu_item" onClick={ () => props.onhandleChangeServiceMode() } >
                    <i className={toggle}></i>
                    Tryb testu widokw
                </li>
    }
    function renderAddTaskButtons(){
        return <div className="app__header_header-buttons-wrapper">
                    <i className="fa fas fa-cart-arrow-down"></i>
                    <div className="app__header_headerButtons">
                        { headerButtons.map( button => 
                            <ConfirmAlertInputTask 
                                    key={button.label}
                                    button={button}
                                    form={ renderCustomForm(button) }
                                    />
                        )}
                    </div>
                </div>
    };
    function renderServiceModeButtons(){
        function getCustomServiceBtnStyle(button){
                return button === props.serviceMode_jobName ? "btn btn-light btn-sm" : "btn btn-outline-light btn-sm"; 
        }
        return <div className="app__header_header-buttons-wrapper-service">
                    <i className="fa fas fa-cogs"></i>
                    <div className="app__header_headerButtons-service">
                        { serviceModeButtons.map( button => 
                            <button key={button} className={getCustomServiceBtnStyle(button)} onClick={ () => props.onhandleChangeServiceModeJob(button)}>{button}</button>
                        )}
                    </div>
                </div>
    };

    const { currentLocation, serviceMode } = props;
    const isSupervisor = userService.isCurrentUserGreaterThanORequalTo("Koordynator");
    return ( 
        <header className="app__header">
            { serviceMode && renderServiceModeButtons() }
            { currentLocation.category === "Maszyna" && renderAddTaskButtons() }
            <div className="app__header_title-barIcon-wrapper">
                <div className="app__header-title">{currentLocation.name}</div>
                { isSupervisor && renderShowDataMenu() }
            </div>
        </header>
    );
}
 
export default ShowDataHeader;