import React from 'react';
import userService from '../services/userService';

const SortingButtons = ({ currentSortingStatus, deleteTaskMode, onChangeSortingStatus, onhandleChangeDeleteTaskMode: onhandleChangeManagerkMode }) => {
 
    const buttons = [
        {label: "W trakcie", path: false },
        {label: "Zakoczone", path: true },
        {label: "Wszystkie", path: "All" }
    ];
    
    function getSortingButtonCustomStyles(buttonPath){
        return (buttonPath === currentSortingStatus ? "btn-secondary" : "btn-outline-secondary") + "  btn btn-sm sorting-buttons-box_sortingButton";
    };
    function getManagerModeButtonStyle(buttonPath){
        return (deleteTaskMode ? "btn-danger" : "btn-outline-danger") + "  btn btn-sm sorting-buttons-box_managerModeButton";
    };
    
    function renderSortingButtons(){
        return buttons.map( button => 
                    <button key={button.label}
                            className={getSortingButtonCustomStyles(button.path)}
                            onClick={ () => onChangeSortingStatus(button.path) }
                            >{button.label}
                    </button>
                )
    };
    function renderManagerModeButton(){
        return <button className={ getManagerModeButtonStyle() } onClick={ onhandleChangeManagerkMode }>Tryb zarzdzania</button>
    }
    const isManager = userService.isCurrentUserGreaterThanORequalTo("Kierownik");
    return ( 
        <div className="sorting-buttons-box">
            <p className="sorting-buttons-box_paragraph">Poka zdarzenia:</p>
            { renderSortingButtons() }
            { isManager && renderManagerModeButton() }
        </div>
        );
}
export default SortingButtons;