import React, { Component } from 'react';
import Advertising from './advertising';
import Loader from './common/loader';
import auth from '../services/authService';
import userService from '../services/userService';
import utils from '../services/utils';
import Joi from 'joi-browser';
import { toast } from 'react-toastify';

class LoginPage extends Component {
    state = { 
        user: {
            name: "",
            password: ""
        },
        isWaitingForSerwerResponse: false
     };
    
    schema = {
        name: Joi.string().min(1).required().error( () => {return { message: "Za krtki login."};}),
        password: Joi.string().min(5).required().error( () => {return { message: "Za krtkie haso."};})
    };
    errorMessage = "Obecnie Twoje konto nie posiada dostpu do adych lokalizacji. Zgo problem przeoonemu lub adminowi."

    handleInputOnChange = ({ currentTarget: input }) => {
        const user = {...this.state.user};
        user[input.name] = input.value;
        this.setState({ user })
    };

    handleLogin = async () => {
        const { name, password } = this.state.user;
        const error = this.validateUser({ name, password });
        if(error) { toast.error(error); return null; }
        this.runLoader(true);
        try{
            const jwt = await auth.login( name, password );
            localStorage.setItem('token', jwt);
            this.redirectUserToNextPath()
        }
        catch(ex){
            if(ex.response && (ex.response.status === 400 || ex.response.status === 403)) {
                toast.error(ex.response.data)
            }
            this.runLoader(false);
        }
    };
    runLoader(mode){
        let { isWaitingForSerwerResponse } = this.state;
        isWaitingForSerwerResponse = mode;
        this.setState({ isWaitingForSerwerResponse })
    }
    redirectUserToNextPath = async () => {
        const userLocalisations = userService.getUserFromJWT().validLocalisations;
        const isSupervisor = userService.isCurrentUserGreaterThanORequalTo("Koordynator");
        if( isSupervisor ) window.location = "/show-data/";
        else if( userLocalisations.length > 1 ) window.location = "/select-localisation/";
        else if( userLocalisations.length === 1 ) window.location = userLocalisations[0].path;
        else { toast.error(this.errorMessage); return null; }
    }
    validateUser(user){
        const { error } = Joi.validate(user, this.schema);
        return error ? error.details[0].message : null;
    };
    renderLoginBox(){
        return (
            <div className="login-page_login_box">
                <h1>Zaloguj si</h1>
                <div className="login-page_login_box__input-box">
                    <div className="login-page_login_box__input-box_group">
                        <p>Login</p>
                        <input name="name"
                                autoComplete="off"
                                autoFocus
                                onChange={this.handleInputOnChange}
                                onKeyPress={ (target) => utils.runFunctionAfterPressEnter(target, this.handleLogin) }
                        ></input>
                    </div>
                </div>
                <div className="login-page_login_box__input-box">
                    <div className="login-page_login_box__input-box_group">
                        <p>Haso</p>
                        <input name="password"
                            autoComplete="off"
                            type="password"
                            onChange={this.handleInputOnChange}
                            onKeyPress={ (target) => utils.runFunctionAfterPressEnter(target, this.handleLogin) }
                        ></input>
                    </div>
                </div>
                <div className="login-page_login_box__button-group">
                <button onClick={ this.handleLogin }
                        >Zaloguj si</button>
                </div>
            
            </div>
        );
    }
    render() { 
        
        return ( 
            <React.Fragment>
                <header className="app__header">
                    <div className="app__header-title">FILMAG</div>
                </header>
                <div className="app__content">
                    <Advertising />
                    <div className="login-page">
                        { this.state.isWaitingForSerwerResponse && <Loader /> }
                        { !this.state.isWaitingForSerwerResponse && this.renderLoginBox() }
                    </div>
                </div>
            </React.Fragment>
         );
    }
}
 
export default LoginPage;

