import React, { Component } from 'react';
import LinkWithButton from './common/linkWithButton.js';
import ConfirmAlert from './common/confirmAlert.js';
import ConfirmAlertSettings from './confirmAlertSettings';
import LogoutButtonBox from './logoutButtonBox';
import InputCustomSelect from './common/inputCustomSelect.js';
import CustomCheckBox from './common/customCheckBox.js';
import userService from '../services/userService';
import { toast } from 'react-toastify';
import Joi from 'joi-browser';

class SettingsUsers extends Component {
    state = { 
        users: [],
        };

    form_deleteUser = {
        title: "Potwierd usunicie konta",
        btn_label: <p><span className="user__content_btn-label_full">Usu konto</span><span className="user__content_btn-label_short">X</span></p>,
        btn_css: "btn btn-danger btn-sm",
        action: (user) => this.handleDeleteUser(user)
    };
    form_resetPassword = {
        title: "Resetowanie haso",
        btn_label: <div><span className="user__content_btn-label_full">Resetuj haso</span><span className="user__content_btn-label_short">R</span></div>,
        message: `Resetujesz haso uytkownika. Nowe haso: 12345.`, 
        btn_css: "btn btn-secondary btn-sm",
        action: (user) => this.handleResetPassword(user)
    };
    form_addteUser = {
        title: "Dodaj konto uytkownika",
        p_label: "Login",
        btn_label: "Dodaj nowe konto",
        btn_css: "btn btn-success btn-sm",
        action: (user) => this.handleAddUser(user)
    };
    columns = [
        {label: "#", path: "#"},
        {label: "Pracownik", path: "name"},
        {label: "Stanowisko", path: "userJob"},
        {label: <i className="fa fas fa-desktop"></i>, path: "status"},
        {label:  <div><span className="user__content_btn-label_full">Magazyn</span><span className="user__content_btn-label_short">MG</span></div>, path: "Magazyn"},
        {label:  <div><span className="user__content_btn-label_full">Maszyny</span><span className="user__content_btn-label_short">MS</span></div>, path: "Maszyny"},
        {label:  <div><span className="user__content_btn-label_full">Utrzymanie ruchu</span><span className="user__content_btn-label_short">UR</span></div>, path: "Utrzymanie ruchu"},
        {label: "", path: "btn-reset"}
    ];
    
    options = [
        {name: "Operator" },
        {name: "Magazynier" },
        {name: "Prac. utrz. ruchu" },
        {name: "Koordynator" },
        {name: "Mistrz" },
        {name: "Kierownik" },
        {name: "Dyrektor" },
        {name: "Prezes" },
        {name: "Admin" },
        {name: "SuperAdmin" }
    ];
    schema = {
        name: Joi.string().min(5).required().error( () => {return { message: "Podana nazwa jest za krtka. Minimum to 5 znakw."};}),
        password: Joi.string().min(5).required().error( () => {return { message: "Podane haso jest za krtkie. Minimum to 5 znakw."};})
    };
    
    constructor(){
        super();
        const isAdmin = userService.isCurrentUserGreaterThanORequalTo("Admin");
        if( isAdmin ) this.columns.push( {label: "", path: "btn-delete" } )
    };
    
    async componentDidMount(){
        this.setState({ users: await userService.getUsers() });
    };
    
    handleDeleteUser = async (userToDelete) => {
        try{
            const user = await userService.deleteUser(userToDelete);
            let users = [ ...this.state.users ];
            const index = users.findIndex(u => u._id === user._id);
            if(index < 0) return;
            users.splice(index, 1)
            this.setState({ users });
        }catch(ex){
            if(ex && ex.response) toast.error(ex.response.data)
        }
    };

    handleAddUser = async newUser => {
        try{
            let user = {name: newUser.name, password: "12345"}
            const error = this.validateNewUser(user);
            if(error) { toast.error(error); return null; }

            user = await userService.addUser( user );
            let users = [ ...this.state.users ];
            users.unshift(user);
            this.setState({ users });
        }catch(ex){
            if(ex && ex.response) toast.error(ex.response.data)
        }
    };

    handleResetPassword = async user => {
        try{
            await userService.resetUserPassword( {_id: user._id, password:  "12345"} );
            toast.info("Zmieniono haso uytkownika.")
        }
        catch (ex) {
            if(ex && ex.response) toast.error(ex.response.data);
        }

    };

    validateNewUser(user){
        const { error } = Joi.validate(user, this.schema);
        return error ? error.details[0].message : null;
    };
    updateUserPermissions = async ( {userN, area} ) => {
        let user = this.chamgeUserPermisions( userN, area );
        user = await userService.updateUser(user);
        let users = [ ...this.state.users ];
        const index = users.findIndex(u => u._id === user._id);
        users[index] = user;
        this.setState({ users });
    };
    chamgeUserPermisions( user, area ){
        const index = user.permissions.indexOf( area );
        if(index >= 0) user.permissions.splice(index,1);
        else user.permissions.push(area);
        return user;
    }
    checkDisabledOptions(){
        return this.options.map( option => option = {name: option.name, isDisabled: userService.isCurrentUserLessThan(option.name)} )
    }
    selectOptions(options, currentValue){
        const isOptionButtonDissabled = userService.isCurrentUserLessThanORequalTo(currentValue);
        return isOptionButtonDissabled ? options.filter( o => o.name === currentValue ) : options.filter( o => userService.isCurrentUserGreaterThan(o.name) ) ;
    }
    
    isChecked(userPermissions, area){ return userPermissions.indexOf( area ) >= 0 };
    
    renderCheckBox(user, area){
        const isDisbaled = !userService.isCurrentUserGreaterThan(user.job) ? true : false;
        return  <CustomCheckBox 
                    item={ {userN: user, area} }
                    isChecked={ this.isChecked(user.permissions, area) }
                    isDisabled = { isDisbaled }
                    action={ this.updateUserPermissions }
                    />
    };

    
    renderUserStatus(user){
        return <div className="user__status">
                    <div className={ user.isLogged ? "status_dot-ok" : "status_dot-ng"}></div>
                    <div className="invisible">invisible text. Do not remove.</div>
                </div>
    }
    renderButton(user, form){
        const isDisbaled = !userService.isCurrentUserGreaterThan(user.job) ? true : false;
        return <ConfirmAlert 
                    item ={user}
                    itemRepresentation={user.name}
                    form = {form}
                    disableButton = { isDisbaled }
                    />
    };
    renderInputCustomSelect( user ) {
        const isDisabled = !userService.isCurrentUserGreaterThan(user.job) ? true : false;
        return <InputCustomSelect 
                    item = { user._id }
                    currentValue = { user.job }
                    isDisabled = { isDisabled }
                    options = { this.checkDisabledOptions() }
                    onChange = { this.handleInputOnChange }
                    selectOptions = { this.selectOptions }
                    />
    };
    handleInputOnChange = async ({ currentTarget: input }) => {
        const users = [...this.state.users];
        const index = users.findIndex( u => u._id === input.name);
        let user = users[index];
            user.job = input.value;
            users[index] = await userService.updateUser(user);
        this.setState({ users })
    };
    renderAddUserButton(){ return <ConfirmAlertSettings form={this.form_addteUser} /> };

    renderCellContent = (user, column) => {
        const { users } = this.state;

        if(column.path === "#") return users.indexOf(user) + 1;
        else if(column.path === "name") return user.name;
        else if(column.path === "userJob") return this.renderInputCustomSelect(user);
        else if(column.path === "status") return this.renderUserStatus(user);
        else if(column.path === "Magazyn") return this.renderCheckBox(user, "Magazyn");
        else if(column.path === "Maszyny") return this.renderCheckBox(user, "Maszyna");
        else if(column.path === "Utrzymanie ruchu") return this.renderCheckBox(user, "Utrzymanie ruchu");
        else if(!user.isDone && column.path === "btn-reset" ) return this.renderButton(user, this.form_resetPassword)// kolumna dla przycisku resetu hasa
        else if(!user.isDone && column.path === "btn-delete" ) return this.renderButton(user, this.form_deleteUser) // kolumna dla przycisku usunicia konta
    };
    renderHeader(columns, mainDivCSS, contentCSS ){  // podobne
        return <div className={ mainDivCSS }> 
                    { columns.map( column => <div key={column.path} className={ contentCSS }>{column.label}</div> )}
                </div>
    }
    renderUsers(users, mainDivCSS, wrapperCSS, contentCSS){  // podobne
        return <div className={ mainDivCSS }>
                    {users.map( user => 
                        <div key={user._id}
                                className={ wrapperCSS }>
                                { this.columns.map( column => 
                                    <div key={ user._id + column.path } className={ contentCSS }
                                        >{ this.renderCellContent(user, column)}
                                    </div>
                                    ) }
                        </div>
                    )}
                </div>
    }
    

    render() { 
        const isAdmin = userService.isCurrentUserGreaterThanORequalTo("Admin");

        return ( 
        <div className="app__content">
            <div className="app__container-buttons">
                <div className="settings__switch-buttons">
                    <LinkWithButton label="Wywietl zdarzenia" path="/show-data" css={"btn btn-sm btn-primary"} />
                    <LinkWithButton label="Lokalizacje" path="/settings/localisations" />
                    <LinkWithButton label="Mj profil" path="/settings/user" />
                    { isAdmin && this.renderAddUserButton() }
                </div>
                <LogoutButtonBox />
            </div>

            <div className="users">
                { this.renderHeader(this.columns, "user user__header", "user__content") }
                { this.renderUsers(this.state.users, "user__wrapper", "user", "user__content") }
            </div>
        </div>
         );
    }
}
 
export default SettingsUsers;