import React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import Joi from 'joi-browser';
import ConfirmAlertInput from './common/confirmAlertInput.js';
import { toast } from 'react-toastify';

class ConfirmAlertInputPartialDelivery extends ConfirmAlertInput {
    state = {
      
    };
    schema = { partialQty: Joi.number().min(1).max(9999).required().error( () => {return { message: this.errorMessage }; }) }
    inputValue = 0;
    originalPartialQty = 0;
    errorMessage = "Podana ilo musi by okrelona jako liczba i mieci si w zakresie 1-9999.";
    componentDidMount(){
      this.originalPartialQty = this.props.task.partialQty;
      this.setState({ item: this.props.task })
    };
    handleInputOnChange_partialDelivery = ({ currentTarget: input }) => {
      this.inputValue = input.value;
    };
    resetTaskData(){
      this.inputValue = 0;
    }
    doSubmit(onClose){
      const {item} = this.state;
      const error = this.validate({partialQty: this.inputValue});
      if(error) { toast.error(error); return null; }
      
      onClose();
      if(item.qty < item.partialQty + parseInt(this.inputValue)) {
        this.confirmGreaterDeliveryThanOrder(item);
      }
      else {
        item.partialQty += parseInt(this.inputValue);
        this.props.form.action(item);
      }
    }
    confirmGreaterDeliveryThanOrder = (item) => {
      confirmAlert({
        title: "Ostrzeenie!",
        message: "czna dostawa bdzie wiksza ni zamwienie. Zatwierd dostaw lub j odrzu.",
        buttons: [
          {
            label: 'Zatwierdzam',
            onClick: () => { item.partialQty += parseInt(this.inputValue); this.props.form.action(item) }
          },
          {
            label: 'Odrzucam',
            onClick: () =>{ return }
          }
        ]
      })
    };
    submit = () => {
      this.resetTaskData();
      confirmAlert({
        customUI: ({ onClose }) => {
              const { title } = this.props.form;
              return (
                <div className="confirmAlertInput">
                  <h1>{title}</h1>
                  <div className="confirmAlertInput__input-box">
                    { this.renderInputBoxGroup("Ilo", "partialQty", "number", "Podaj ilo jak teraz dostarczasz", onClose, "autoFocus", this.handleInputOnChange_partialDelivery) }
                  </div>
                  <div>
                    <p>* Podana warto zostanie dodana do sumy zrealizowanej dostawy.</p>
                  </div>
                  <div className="confirmAlertInput__button-group">
                    { this.renderButton("Dodaj", () => this.doSubmit(onClose) ) }
                    { this.renderButton("Anuluj", onClose ) }
                  </div>
                </div>
              )
            } })
    };
    
    render() { 
        return ( 
              <button 
                className={ this.props.form.btn_css }
                onClick={this.submit}
                >{this.props.form.btn_label}
            </button>
            );
    }
}

export default ConfirmAlertInputPartialDelivery;